* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  text-transform: uppercase;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

:root {
  --blue: #00acdc;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__list :first-child {
  margin: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  transition: all .3s;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: var(--blue);
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.is-active {
  color: var(--blue);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
